import { doLogin } from '@/network/login.js'
import { isPoneAvailable, isPasswordAvailable} from '@/common/index.js'

import { mapMutations, mapActions } from 'vuex'


import baseUrl  from '@/common/helper.js'

export default {
	components: {
		
	},
	data() {
		return {
			userPhone: '',
			userPassword: '',
			showUserPhone: false,
			showUserPassword: false
		}
	},
	methods: {
		...mapMutations(['setLogin']),
		...mapActions(['doGetMyUserInfo']),
		loginClick(e) {
			const value = e.detail.value;
			const userData = {
				userPhone: value.userPhone,
				userPassword: value.userPassword
			}
			if(isPoneAvailable(value.userPhone)) {
				if(isPasswordAvailable(value.userPassword)) {
					doLogin(userData).then(res => {
						const data = res.data;
						console.log(res)
						if(res.status === 200) {
							if(data.code === 2000) {
								const userInfo = res.data.data
								
								//存储 用户微信息(全局userInfo、hasLogin和本地仓储userInfo)
								this.setLogin(userInfo)
								
								//请求用户详细信息
								this.doGetMyUserInfo()
								
								uni.showToast({
									title: '正在登录',
									icon: 'none'
								})
								
								uni.reLaunch({
									url: '/pages/index/index'
								})
							}else if(data.code === 4003) {
								uni.showToast({
									title: '密码错误',
									icon: 'none'
								})
							}else if(data.code === 4004) {
								uni.showToast({
									title: '号码未注册,请先注册再登录',
									icon: 'none'
								})
							}else if(data.code === 4000) {
								uni.showToast({
									title: '手机号码或者密码不能为空',
									icon: 'none'
								})
							}else {
								console.log('11111')
								uni.showToast({
									title: '您可能与服务器断开了连接',
									icon: 'none'
								})
							}
							
							
							
						}else {
							console.log('22222')
							uni.showToast({
								title: '您可能与服务器断开了连接',
								icon: 'none'
							})
						}
						
						
					}).catch(err => {
						console.log('33333')
						console.log(err)
						uni.showToast({
							title: '您可能与服务器断开了连接',
							icon: 'none'
						})
					})
				}else {
					uni.showToast({
						title: '密码错误',
						icon: 'none'
					})
				}
				
			}else {
				uni.showToast({
					title: '请输入正确手机号',
					icon: 'none'
				})
			}
		},
		delUserPhone() {
			setTimeout(() => {
				this.userPhone = '';
				this.showUserPhone = false
			}, 80)
		},
		delUserPassword() {
			setTimeout(() => {
				this.userPassword = '';
				this.showUserPassword = false
			}, 80)
		},
		//监听输入
		handlerUserPhone(e) {
			if(e.detail.value.length > 0) {
				this.showUserPhone = true
			}else if(e.detail.value.length === 0) {
				this.showUserPhone = false
			}
		},
		handlerUserPassword(e) {
			if(e.detail.value.length > 0) {
				this.showUserPassword = true
			}else if(e.detail.value.length === 0) {
				this.showUserPassword = false
			}
		},
		
		
		toRegister1() {
			uni.navigateTo({
				url: '/components/content/register/register1'
			})
		},
		toChangepwd1() {
			uni.navigateTo({
				url: '/components/content/changepwd/changepwd1'
			})
		}
	},
	onNavigationBarButtonTap(options) {
		if(options.index === 0) {
			uni.switchTab({
				url: "/pages/index/index"
			})
		}
	}
}
